import React from 'react'

import './Header.css'
import HeaderOption from './HeaderOption';

import SearchIcon from '@mui/icons-material/Search';
import HomeRoundedIcon from '@mui/icons-material/HomeRounded';
import SupervisorAccountRoundedIcon from '@mui/icons-material/SupervisorAccountRounded';
import WorkIcon from '@mui/icons-material/Work';
import SmsRoundedIcon from '@mui/icons-material/SmsRounded';
import NotificationsRoundedIcon from '@mui/icons-material/NotificationsRounded';
import AccountCircleRoundedIcon from '@mui/icons-material/AccountCircleRounded';
import AppsRoundedIcon from '@mui/icons-material/AppsRounded';

function Header() {
    return (
        <div className='header'>
            <div className='header__left'>
                <img
                    src="https://upload.wikimedia.org/wikipedia/commons/8/81/LinkedIn_icon.svg"
                    alt="linkedin-logo" />
                <div className="header__search">
                    <SearchIcon />
                    <input type="text" placeholder="search" />
                </div>
            </div>
            <div className='header__right'>
                <HeaderOption title="Home" Icon={HomeRoundedIcon} />
                <HeaderOption title="My network" Icon={SupervisorAccountRoundedIcon} />
                <HeaderOption title="Job" Icon={WorkIcon} />
                <HeaderOption title="Messages" Icon={SmsRoundedIcon} />
                <HeaderOption title="Notifications" Icon={NotificationsRoundedIcon} />
                <HeaderOption title="Me" Icon={AccountCircleRoundedIcon} />
                <HeaderOption title="Work" Icon={AppsRoundedIcon} />
            </div>

        </div>
    )
}

export default Header