import React from 'react'
import './Widget.css'
import InfoIcon from "@material-ui/icons/Info"
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import AddRoundedIcon from '@mui/icons-material/AddRounded';
function Widget() {

    const newsArticle = (name, description) => (
        <div className="widget__article">
            <div className="widget__articleLeft">
                <AccountCircleIcon />
            </div>

            <div className="widget__articleRight">
                <h4>{name}</h4>
                <p>{description}</p>
                <div className="widget__button__follow">
                    <AddRoundedIcon />
                    <p>Follow</p>
                </div>
            </div>

        </div>
    )

    return (
        <div className='widget'>
            <div className="widget__header">
                <h2>Add to your feed</h2>
                <InfoIcon />
            </div>
            {newsArticle("Juanito Alcachofa", 'React and Angular developer with 50+ years of experience in Cobol')}
            {newsArticle("Mario Felipe", 'Psicloga, Especialista en Talento Humano y Coach certificado')}
            {newsArticle("Diego Rojas", 'Analista de reclutamiento')}
            {newsArticle("Daniela Fonseca", 'Te ayudo a conseguir trabajo')}
        </div>
    )
}

export default Widget